/*=============== 
navbar
==================*/


header nav ul {
  display: flex;
  font-size: 1.4rem;
}

header nav ul li {
  list-style: none;
  font-size: 1em;
}

header nav ul li a {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-size: 0.4em;
  padding: 24px;
  transition: all 250ms linear 0s;
}
header nav ul li h6 {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-size: 0.7em;
  padding: 10px;
  margin-top: 5px;
  transition: all 250ms linear 0s;
}

header nav ul li a:hover {
  background: rgba(255,255,255, 0.15);
  border-radius: 10px;
  color: white;
  animation-duration: 2s;
  animation-name: slideIn;
}



